// <RATING>
// Снипет (HTML): rating
.rating {
    display: flex;
    align-items: flex-end;
    font-size: 12px;
    line-height: 0.75;
    transition: opacity 0.3s ease 0s;

    @media (max-width:$smallTablet) {
        font-size: 10px;
    }
}

.rating.rating_sending {
    opacity: 0.2;
}

.rating.rating_set .rating__active,
.rating.rating_set .rating__item {
    cursor: pointer;
}

.rating__body {
    position: relative;
}

.rating__body::before {
    content: "\e908  \e908  \e908  \e908  \e908";
    @extend %ic;
    display: block;
    color: rgba(201, 210, 234, 0.78);
}

.rating__active {
    position: absolute;
    width: 0%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
}

.rating__active::before {
	content: "\e908  \e908  \e908  \e908  \e908";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    color: #ffd300;
    @extend %ic;
    display: block;
}

.rating__items {
    display: flex;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.rating__item {
    flex: 0 0 20%;
    height: 100%;
    opacity: 0;
}

.rating__value {
    font-size: 50%;
    line-height: 1;
    padding: 0px 0px 0px 10px;
    display: none;
}
